/**
 * å…¨å±€é”™è¯¯æ•è·å’Œæ—¥å¿—ç³»ç»Ÿ
 * åªåœ¨å¼€å‘æ¨¡å¼ä¸‹å¯ç”¨
 */

class ErrorLogger {
  constructor() {
   // this.isDev = process.env.NODE_ENV === 'development'
    this.isDev = false;
    this.logEndpoint = 'http://localhost:3001/api/logs'
    this.logs = []
    this.maxLogs = 100 // æœ€å¤§æ—¥å¿—æ•°é‡
    
    if (this.isDev) {
      this.init()
    }
  }

  init() {
    console.log('ğŸ” å¼€å‘æ¨¡å¼ï¼šé”™è¯¯æ—¥å¿—ç³»ç»Ÿå·²å¯ç”¨ï¼ˆå·²ä¿®å¤æ— é™å¾ªç¯é—®é¢˜ï¼‰')

    // æ•è·Vueé”™è¯¯
    this.setupVueErrorHandler()

    // æ•è·å…¨å±€JavaScripté”™è¯¯
    this.setupGlobalErrorHandler()

    // æ•è·Promiseæœªå¤„ç†çš„rejection
    this.setupPromiseRejectionHandler()

    // æ•è·ç½‘ç»œè¯·æ±‚é”™è¯¯ï¼ˆå·²åŠ å¼ºè¿‡æ»¤ï¼‰
    this.setupNetworkErrorHandler()

    // å®šæœŸå‘é€æ—¥å¿—åˆ°åç«¯
    this.startLogSender()
  }

  setupVueErrorHandler() {
    // è¿™ä¸ªä¼šåœ¨Vueæ’ä»¶å®‰è£…æ—¶è®¾ç½®
    this.vueErrorHandler = (err, vm, info) => {
      const errorLog = {
        type: 'vue-error',
        timestamp: new Date().toISOString(),
        message: err.message,
        stack: err.stack,
        componentName: vm?.$options.name || 'Unknown',
        componentInfo: info,
        url: window.location.href,
        userAgent: navigator.userAgent,
        route: vm?.$route?.path || window.location.hash.replace('#', '') || '/'
      }

      this.addLog(errorLog)
      console.error('ğŸš¨ Vueé”™è¯¯:', errorLog)
    }

    // æ•è·Vueè­¦å‘Š
    this.vueWarnHandler = (msg, vm, trace) => {
      const warnLog = {
        type: 'vue-warn',
        timestamp: new Date().toISOString(),
        message: msg,
        trace: trace,
        componentName: vm?.$options.name || 'Unknown',
        url: window.location.href,
        userAgent: navigator.userAgent,
        route: vm?.$route?.path || window.location.hash.replace('#', '') || '/'
      }

      this.addLog(warnLog)
      console.warn('âš ï¸ Vueè­¦å‘Š:', warnLog)
    }
  }

  setupGlobalErrorHandler() {
    window.addEventListener('error', (event) => {
      const errorLog = {
        type: 'javascript-error',
        timestamp: new Date().toISOString(),
        message: event.message,
        filename: event.filename,
        lineno: event.lineno,
        colno: event.colno,
        stack: event.error?.stack,
        url: window.location.href,
        userAgent: navigator.userAgent
      }
      
      this.addLog(errorLog)
      console.error('ğŸš¨ JavaScripté”™è¯¯:', errorLog)
    })
  }

  setupPromiseRejectionHandler() {
    window.addEventListener('unhandledrejection', (event) => {
      const errorLog = {
        type: 'promise-rejection',
        timestamp: new Date().toISOString(),
        message: event.reason?.message || String(event.reason),
        stack: event.reason?.stack,
        url: window.location.href,
        userAgent: navigator.userAgent
      }
      
      this.addLog(errorLog)
      console.error('ğŸš¨ Promise Rejection:', errorLog)
    })
  }

  setupNetworkErrorHandler() {
    // æ‹¦æˆªfetchè¯·æ±‚
    const originalFetch = window.fetch
    window.fetch = async (...args) => {
      try {
        const response = await originalFetch(...args)
        if (!response.ok) {
          // è·³è¿‡æ—¥å¿—æœåŠ¡å™¨ç›¸å…³çš„é”™è¯¯ï¼Œé¿å…æ— é™å¾ªç¯
          const url = args[0]
          if (url && (url.includes('localhost:3001') || url.includes('/api/logs'))) {
            return response
          }

          const errorLog = {
            type: 'network-error',
            timestamp: new Date().toISOString(),
            message: `HTTP ${response.status}: ${response.statusText}`,
            url: args[0],
            status: response.status,
            statusText: response.statusText,
            currentPage: window.location.href
          }

          this.addLog(errorLog)
          console.error('ğŸš¨ ç½‘ç»œé”™è¯¯:', errorLog)
        }
        return response
      } catch (error) {
        // è·³è¿‡æ—¥å¿—æœåŠ¡å™¨ç›¸å…³çš„é”™è¯¯ï¼Œé¿å…æ— é™å¾ªç¯
        const url = args[0]
        if (url && (url.includes('localhost:3001') || url.includes('/api/logs'))) {
          throw error // é‡æ–°æŠ›å‡ºé”™è¯¯ï¼Œä½†ä¸è®°å½•æ—¥å¿—
        }

        const errorLog = {
          type: 'network-error',
          timestamp: new Date().toISOString(),
          message: error.message,
          stack: error.stack,
          url: args[0],
          currentPage: window.location.href
        }

        this.addLog(errorLog)
        console.error('ğŸš¨ ç½‘ç»œè¯·æ±‚å¤±è´¥:', errorLog)
        throw error
      }
    }
  }

  addLog(logData) {
    if (!this.isDev) return

    // åªä¿å­˜é”™è¯¯ã€è­¦å‘Šå’Œå¼‚å¸¸ï¼Œè¿‡æ»¤æ‰infoå’Œdebug
    const importantTypes = ['vue-error', 'vue-warn', 'javascript-error', 'promise-rejection', 'network-error']
    const isManualError = logData.type === 'manual-log' && ['error', 'warn'].includes(logData.level)

    if (!importantTypes.includes(logData.type) && !isManualError) {
      // åªåœ¨æ§åˆ¶å°æ˜¾ç¤ºï¼Œä¸ä¿å­˜åˆ°æœåŠ¡å™¨
      return
    }

    this.logs.push(logData)

    // é™åˆ¶æ—¥å¿—æ•°é‡
    if (this.logs.length > this.maxLogs) {
      this.logs = this.logs.slice(-this.maxLogs)
    }

    // ç«‹å³å°è¯•å‘é€è¿™ä¸ªé”™è¯¯æ—¥å¿—
    this.sendLogToServer(logData)
  }

  // æ‰‹åŠ¨è®°å½•æ—¥å¿—çš„æ–¹æ³•
  log(level, message, data = {}) {
    if (!this.isDev) return

    // å…ˆåœ¨æ§åˆ¶å°æ˜¾ç¤º
    console.log(`ğŸ“ [${level.toUpperCase()}]`, message, data)

    // åªæœ‰é”™è¯¯å’Œè­¦å‘Šæ‰å‘é€åˆ°æœåŠ¡å™¨
    if (level === 'error' || level === 'warn') {
      const logData = {
        type: 'manual-log',
        level: level,
        timestamp: new Date().toISOString(),
        message: message,
        data: data,
        url: window.location.href,
        userAgent: navigator.userAgent,
        route: window.location.hash.replace('#', '') || '/'
      }

      this.addLog(logData)
    }
  }

  async sendLogToServer(logData) {
    // é¿å…æ— é™å¾ªç¯ï¼šå¦‚æœæ˜¯æ—¥å¿—æœåŠ¡å™¨çš„ç½‘ç»œé”™è¯¯ï¼Œä¸å†å‘é€
    if (logData.type === 'network-error' &&
        (logData.url === this.logEndpoint ||
         logData.url === `${this.logEndpoint}/batch`)) {
      console.warn('ğŸ“¤ è·³è¿‡æ—¥å¿—æœåŠ¡å™¨é”™è¯¯ï¼Œé¿å…æ— é™å¾ªç¯')
      return
    }

    // é¿å…æ— é™å¾ªç¯ï¼šå¦‚æœé”™è¯¯æ¶ˆæ¯åŒ…å«æ—¥å¿—æœåŠ¡å™¨ç›¸å…³å†…å®¹ï¼Œä¸å†å‘é€
    if (logData.message &&
        (logData.message.includes('localhost:3001') ||
         logData.message.includes('Failed to fetch') ||
         logData.message.includes('NetworkError') ||
         logData.message.includes('ERR_CONNECTION_REFUSED'))) {
      console.warn('ğŸ“¤ è·³è¿‡ç½‘ç»œç›¸å…³é”™è¯¯ï¼Œé¿å…æ— é™å¾ªç¯')
      return
    }

    try {
      // è®¾ç½®è¶…æ—¶å’Œé‡è¯•é™åˆ¶
      const controller = new AbortController()
      const timeoutId = setTimeout(() => controller.abort(), 5000) // 5ç§’è¶…æ—¶

      await fetch(this.logEndpoint, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(logData),
        signal: controller.signal
      })

      clearTimeout(timeoutId)
    } catch (error) {
      // é™é»˜å¤„ç†å‘é€å¤±è´¥ï¼Œé¿å…æ— é™å¾ªç¯
      console.warn('ğŸ“¤ æ—¥å¿—å‘é€å¤±è´¥:', error.message)
      // ä¸è¦å†æ¬¡è®°å½•è¿™ä¸ªé”™è¯¯ï¼Œé¿å…æ— é™å¾ªç¯
    }
  }

  startLogSender() {
    // æ¯30ç§’æ‰¹é‡å‘é€ä¸€æ¬¡æ—¥å¿—
    setInterval(() => {
      if (this.logs.length > 0) {
        this.sendBatchLogs()
      }
    }, 30000)
  }

  async sendBatchLogs() {
    if (this.logs.length === 0) return

    // è¿‡æ»¤æ‰æ—¥å¿—æœåŠ¡å™¨ç›¸å…³çš„ç½‘ç»œé”™è¯¯ï¼Œé¿å…æ— é™å¾ªç¯
    const filteredLogs = this.logs.filter(log => {
      // è¿‡æ»¤ç½‘ç»œé”™è¯¯
      if (log.type === 'network-error' &&
          (log.url === this.logEndpoint ||
           log.url === `${this.logEndpoint}/batch`)) {
        return false
      }

      // è¿‡æ»¤åŒ…å«æœåŠ¡å™¨ç›¸å…³é”™è¯¯æ¶ˆæ¯çš„æ—¥å¿—
      if (log.message &&
          (log.message.includes('localhost:3001') ||
           log.message.includes('Failed to fetch') ||
           log.message.includes('NetworkError') ||
           log.message.includes('ERR_CONNECTION_REFUSED'))) {
        return false
      }

      return true
    })

    // æ¸…ç©ºåŸå§‹æ—¥å¿—æ•°ç»„ï¼Œé¿å…é‡å¤å¤„ç†
    this.logs = []

    if (filteredLogs.length === 0) {
      return
    }

    try {
      // è®¾ç½®è¶…æ—¶
      const controller = new AbortController()
      const timeoutId = setTimeout(() => controller.abort(), 10000) // 10ç§’è¶…æ—¶

      await fetch(`${this.logEndpoint}/batch`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          logs: filteredLogs,
          session: this.getSessionId()
        }),
        signal: controller.signal
      })

      clearTimeout(timeoutId)
      console.log('ğŸ“¤ æ‰¹é‡æ—¥å¿—å‘é€æˆåŠŸ')
    } catch (error) {
      console.warn('ğŸ“¤ æ‰¹é‡æ—¥å¿—å‘é€å¤±è´¥:', error.message)
      // ä¸è¦é‡æ–°æ·»åŠ åˆ°æ—¥å¿—é˜Ÿåˆ—ï¼Œé¿å…æ— é™é‡è¯•
    }
  }

  getSessionId() {
    if (!this.sessionId) {
      this.sessionId = 'session_' + Date.now() + '_' + Math.random().toString(36).substr(2, 9)
    }
    return this.sessionId
  }

  // è·å–å½“å‰æ‰€æœ‰æ—¥å¿—
  getAllLogs() {
    return this.logs
  }

  // æ¸…ç©ºæ—¥å¿—
  clearLogs() {
    this.logs = []
    console.log('ğŸ—‘ï¸ æ—¥å¿—å·²æ¸…ç©º')
  }
}

// åˆ›å»ºå…¨å±€å®ä¾‹
const errorLogger = new ErrorLogger()

// Vueæ’ä»¶å®‰è£…å‡½æ•°
const ErrorLoggerPlugin = {
  install(Vue) {
    if (!errorLogger.isDev) return

    // è®¾ç½®Vueé”™è¯¯å¤„ç†å™¨
    Vue.config.errorHandler = errorLogger.vueErrorHandler

    // è®¾ç½®Vueè­¦å‘Šå¤„ç†å™¨
    Vue.config.warnHandler = errorLogger.vueWarnHandler

    // æ·»åŠ åˆ°VueåŸå‹ï¼Œæ–¹ä¾¿åœ¨ç»„ä»¶ä¸­ä½¿ç”¨
    Vue.prototype.$logger = {
      info: (message, data) => errorLogger.log('info', message, data),
      warn: (message, data) => errorLogger.log('warn', message, data),
      error: (message, data) => errorLogger.log('error', message, data),
      debug: (message, data) => errorLogger.log('debug', message, data)
    }
    
    // å…¨å±€æ··å…¥ï¼Œåœ¨æ¯ä¸ªç»„ä»¶ä¸­éƒ½å¯ä»¥ä½¿ç”¨
    Vue.mixin({
      created() {
        // ç»„ä»¶åˆ›å»ºæ—¶è®°å½•æ—¥å¿—
        if (this.$options.name) {
          this.$logger.debug(`ç»„ä»¶åˆ›å»º: ${this.$options.name}`)
        }
      },
      errorCaptured(err, vm, info) {
        // æ•è·å­ç»„ä»¶é”™è¯¯
        errorLogger.vueErrorHandler(err, vm, info)
        return false // ç»§ç»­å‘ä¸Šä¼ æ’­é”™è¯¯
      }
    })
  }
}

export default ErrorLoggerPlugin
export { errorLogger }
